* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #fff;
}
body {
  background: #191919;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.next_days {
  font-family: sans-serif;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
.cars {
  width: 100px;
  height: 150px;
  padding: 10px;
  color: #ffffff;
  background: red;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 10px;
  transition: 0.5s;
}
.cars:hover {
  transform: scale(1.15);
  transition: 0.3s;
}
.cars:hover img {
  transform: scale(1.5);
  transition: 0.5s;
}
.cars img {
  width: 64px;
  transition: 0.5s;
}
.temp {
  font-size: 1em;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cars h1 {
  font-size: 1em;
}
.cars h4 {
  font-size: 0.5em;
}

.currentConditions {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
  background: #191919;
}
.currentConditions h1 {
  width: 100%;
}
.currentConditions :nth-child(odd) {
  background: #020202;
}
.ddd {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}
h1 {
  display: flex;
  align-items: center;
  justify-content: center;
}

form {
  display: flex;
  align-items: center;
  justify-content: center;
}
form svg {
  font-size: -webkit-xxx-large;
}
form input {
  font-size: 2rem;
  height: 50px;
  width: 250px;
  color: #000;
}
a {
  position: absolute;
  top: 100%;
  /* display: flex; */
  text-align: center;
  width: 100%;
  background: black;
}
